import { Component, EventEmitter, h, Host, Prop, Event } from '@stencil/core';
import { SelectOption } from '../../../types/select';

/**
 * @category Data Tables
 * @description Optional component for use with ```<at-table>```.
 * Provides controls for pagination if you would like to
 * have control of the table's pagination.
 */
@Component({
    tag: 'at-table-pagination',
    shadow: false,
})
export class AtTablePagination {
    /**
     * Current page number
     */
    @Prop() current_page: number = 1;

    /**
     * Total number of pages
     */
    @Prop() num_pages: number = 1;

    /**
     * Options provided in dropdown for page sizes.
     */
    @Prop() page_size_options: SelectOption[] = [
        { value: '5' },
        { value: '10' },
        { value: '20' },
        { value: '50' },
        { value: '100' },
    ];

    /**
     * Emits event with ```event.detail``` as the new page number
     */
    @Event() atChange: EventEmitter<number>;

    /**
     * Emits event with ```event.detail``` as the new page size
     */
    @Event() atPageSizeChange: EventEmitter<number>;

    render() {
        return (
            <Host class="mt-8 flex items-center justify-end gap-8">
                <span>Page Size: </span>
                <at-select
                    options={this.page_size_options}
                    value={`${this.page_size_options[0]}`}
                    clearable={false}
                    onAtuiChange={(event: CustomEvent) =>
                        this.atPageSizeChange.emit(parseInt(event.detail))
                    }
                />
                <at-button
                    disabled={this.current_page === 1}
                    type="secondaryText"
                    icon="first_page"
                    onAtuiClick={() => this.atChange.emit(1)}
                />
                <at-button
                    disabled={this.current_page === 1}
                    type="secondaryText"
                    icon="chevron_left"
                    onAtuiClick={() =>
                        this.atChange.emit(this.current_page - 1)
                    }
                />
                <span>
                    Page {this.current_page} of {this.num_pages}
                </span>
                <at-button
                    disabled={this.current_page === this.num_pages}
                    type="secondaryText"
                    icon="chevron_right"
                    onAtuiClick={() =>
                        this.atChange.emit(this.current_page + 1)
                    }
                />
                <at-button
                    disabled={this.current_page === this.num_pages}
                    type="secondaryText"
                    icon="last_page"
                    onAtuiClick={() => this.atChange.emit(this.num_pages)}
                />
            </Host>
        );
    }
}
